import React, { useState, useEffect } from 'react';
import './LocationListings.css';
import data from '../../data/CafeResturants.json';
import TextField from '@mui/material/TextField';
import IconButton from '@mui/material/IconButton';
import SearchIcon from '@mui/icons-material/Search';
import { Link } from 'react-router-dom'
import { useLocation } from 'react-router-dom';
import LocationCards from '../../commons/locationCard/locationCard'
import { Helmet } from 'react-helmet';

function LocationListings() {
  var [searchQuery, setSearchQuery] = useState('');
  const [filteredLocations, setFilteredLocations] = useState(data);
  const location = useLocation();
  const search = useLocation().search;
  const id = new URLSearchParams(search).get("keyId");
  const handleSearch = (val) => {
    setSearchQuery(val)
    var query = val.toLowerCase();
    if(val == ""){
      searchQuery = val
    }else if(searchQuery!==""){
      query = searchQuery.toLowerCase();
    }
    const filtered = data.filter((location) => {
      const name = location.name.toLowerCase();
      const description = location.description.toLowerCase();
      const locationName = location.location.toLowerCase();

      return name.includes(query) || description.includes(query) || locationName.includes(query);
    });

    setFilteredLocations(filtered);
  };
  
  useEffect(() => {
    // Get the element by ID
    if (id !== null && id !== "null") {
      const element = document.getElementById(id);
      // Trigger a click event on the element
      if (element) {
        element.click();
      } else {
        window.location.href = "404"
      }
    }
    const body = document.querySelector('#root');
    body.scrollIntoView({
      behavior: 'smooth'
    }, 500)

    setTimeout(() => {
      if (location?.state?.value) {
        var searchBar = document.getElementById('searchBar')
        searchBar.value = location?.state?.value
        setSearchQuery(location?.state?.value)
        handleSearch(location?.state?.value)
      } else {
        setSearchQuery("")
      }    }, 1000);
  }, []);

  return (
    <div className="location-listings">
       <Helmet>
       <link rel="canonical" href="https://crowndevour.com/location" />
        <title>search cafe and restaurants</title>
        <meta name="description" content="Explore a gastronomic adventure with our curated list of cafes, restaurants, and thelas (food stalls)! Uncover hidden culinary gems and popular dining spots, each with its unique flavor and ambiance. From cozy cafes perfect for a leisurely brunch to vibrant restaurants offering diverse cuisines, and charming thelas serving street delights, our comprehensive guide is your gateway to a world of delectable experiences. Whether you're a food enthusiast or simply seeking the perfect place to indulge your taste buds, our list has something for every palate. Discover, savor, and embark on a journey through the diverse culinary landscape on our page dedicated to the best cafes, restaurants, and thelas in town!" />
      </Helmet>
      <div className='centerDiv padding-5'>
        <TextField
          variant="outlined"
          className='searchBar'
          id='searchBar'
          InputProps={{
            endAdornment: (
              <IconButton color="primary">
                <SearchIcon />
              </IconButton>
            ),
          }}
          type="text"
          placeholder="Search locations by name, description, or location"
          value={searchQuery}
          onChange={(e) => handleSearch(e.target.value)}
        />
      </div>
      <section className="section-sm">
        <div className="container">
          <div className="row">
            <div className="col-lg-12">
              <div className="title text-center">
                <h2 className="mb-5">Posted by this author</h2>
              </div>
            </div>
            {filteredLocations.map((location, index) => (
              <div className="col-lg-4 col-sm-6 mb-4">
                <Link to={'/location/' + location.title} state={{ loc: location }}>
                  <LocationCards data={location} />
                </Link>
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}

export default LocationListings;

